<template>
    <!-- <div v-if="product!=null" class="bg-light"> -->
    <div v-if="product!=null" style="background-color: #F5F5F5">
        <div class="col" v-if="$store.getters.cart_product_length==0">
            <div class="d-flex align-items-center py-5 mt-5">
                <div class="col-4">
                    <img src="@/assets/empty-cart.png" alt="Empty Cart tukarjual.com" class="img-fluid">
                </div>
                <div class="col-8">
                    <h5>Wah, keranjang belanjaan Pian kosong</h5>
                    <small>Yuk, isi dengan barang - barang impianmu</small>
                </div>
            </div>
            <router-link :to="'/c/smart-3'" class="btn btn-block text-white mt-3 tombol" data-dismiss="modal">Mulai Belanja</router-link>
        </div>
        <div v-else class="pt-5 mt-1">
            <div class="col bg-white border-top border-bottom mb-2 py-2" v-for="(item, index) in product" :key="item.no_transaksi">
                <div class="d-flex align-items-center">
                    <input v-model="product[index].checked" type="checkbox" class="mr-2">
                    {{item.product.nama_barang}}
                </div>
                <div class="d-flex align-items-center justify-content-between mt-2">
                    <div class="btn-group">
                        <button :disabled="item.jumlah==1||item.loadingDelete" @click="product[index].jumlah--,editProductInCart(index)" type="button" class="btn btn-light border btn-sm">-</button>
                        <input :disabled="item.loadingDelete" @focusout="$event.target.value = product[index].jumlah" @input="inputJumlah($event.target.value, index)" type="number" class="form-control form-control-sm text-center border-left-0 rounded-0 p-0" min="1" style="width: 1cm" :value="item.jumlah">
                        <button :disabled="item.jumlah==item.product.stock||item.loadingDelete" @click="product[index].jumlah++,editProductInCart(index)" type="button" class="btn btn-light border btn-sm">+</button>
                    </div>
                    <i v-if="item.cancelToken!=null" class="fa fa-spinner fa-spin"></i>
                    <template v-else>Rp {{toCurrency(item.total_price)}}</template>
                     <i v-if="item.loadingDelete" class="fa fa-spinner fa-spin"></i>
                    <i v-else class="fas fa-trash-alt text-black-50" @click="deleteProductInCart(index)"></i>
                </div>
            </div>
            <div class="pb-3 mb-5"></div>
            <div class="fixed-bottom bg-white border-top container-footer">
                <div class="d-flex justify-content-between py-2 mx-2">
                    <div class="d-flex flex-column">
                        <span class="text-muted small">TOTAL BELANJA</span>
                        <span class="font-weight-bold">
                            <i v-if="loading" class="fa fa-spinner fa-spin"></i>
                            <template v-else>Rp {{toCurrency(total)}}</template>
                        </span>
                        <!-- <span class="text-muted small">Total yang Pian bayarkan</span> -->
                    </div>
                    <div class="d-flex align-items-center">
                        <button :disabled="loading" @click="checkout" class="btn btn-lg text-white rounded-0 px-5 tombol">
                            <i v-if="loading" class="fa fa-spinner fa-spin"></i>
                            <template v-else>Pesan</template>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
export default {
    name:"Cart",
    data(){
        return {
            product:null,
            loading:false,
            loadingDelete:false
        }
    },
    computed:{
        total(){
            return this.product.reduce((accumulator, current) => accumulator + current.total_price, 0);
        },
        checked(){
            return this.product.filter(p => p.checked===true).map(p => p.no_transaksi);
        },
    },
    created(){
        if(this.$store.state.login.ifLogin){
            this.axios.get(this.$store.state.url_api + "cart", {
                headers: {
                    "Authorization": "Bearer " + localStorage.token
                },
            }).then(response => {
                this.product = response.data.map(el => {
                    el.timeout = null;
                    el.cancelToken = null;
                    el.loadingDelete = false;
                    el.checked=false;
                    return el;
                });
            })
        }
        // this.product = this.$store.state.cart.cart_product;
    },
    methods:{
        inputJumlah(value, index){
            if(value != "" && parseInt(value) != 0){
                if(parseInt(value)>this.product[index].product.stock){
                    value = this.product[index].product.stock;
                }
                this.product[index].jumlah = value
                this.editProductInCart(index);
            }
        },
        update(index){
            this.loading = true;
            if(this.product[index].cancelToken!=null){
                this.product[index].cancelToken.cancel();
            }
            this.product[index].cancelToken = axios.CancelToken.source();
            this.axios.post(this.$store.state.url_api + "cart/update", {
                transaction_number:this.product[index].no_transaksi,
                quantity:this.product[index].jumlah
            }, {
                headers:{
                    "Accept": "Application/json",
                    "Authorization": "Bearer " + localStorage.token
                },
                cancelToken:this.product[index].cancelToken.token
            }).then(response => {
                this.product[index].timeout = null;
                this.product[index].cancelToken = null;
                this.product[index].total_price = response.data.item.total_price;
                this.loading = false;
                this.$store.commit('MUTATIONS_PRODUCT',response.data.item);
            }).catch(() => {
                this.product[index].cancelToken = null;
            })
        },
        editProductInCart(index){
            if(this.product[index].timeout!=null){
                clearTimeout(this.product[index].timeout); 
            }
            this.product[index].timeout = setTimeout(() => {
                this.update(index)
            }, 500);
            
        },
        deleteProductInCart(index){
            this.product[index].loadingDelete = true;
            this.axios.delete(this.$store.state.url_api + "cart/delete?transaction_number=" + this.product[index].no_transaksi, {
                headers:{
                    "Authorization": "Bearer " + localStorage.token
                },
            }).then(() => {
                this.$store.commit('DELETE_PRODUCT', this.product[index].no_transaksi); 
                this.product.splice(index, 1);
                this.product[index].loadingDelete = false;
            }).catch(() => {
                
            })
        },
        checkout(){
            var checked = this.checked;
            if(this.checked.length==0){
                checked = this.product.map(p => p.no_transaksi);
            }
            this.$router.push({
                path:'address?from=cart&',
                query:{
                    items:checked
                }
            })
        },
        toCurrency(angka) {
            angka += '';
			// angka = angka.replace(".",",");
			var x = angka.split('.');
			var x1 = x[0];
			var x2 = x.length > 1 ? '.' + x[1] : '';
			var rgx = /(\d+)(\d{3})/;
			while (rgx.test(x1)) {
				x1 = x1.replace(rgx, '$1' + '.' + '$2');
			}
			return x1 + x2;
		},
    }
}
</script>